import React from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';

const Features = () => {
  const features = [
    { title: "No Hidden Fees", text: "Enjoy zero maintenance charges and free transactions." },
    { title: "Smart Budgeting", text: "Track expenses and set budgets with ease." },
    { title: "Secure & Fast", text: "End-to-end encryption ensures your data is safe." }
  ];

  return (
    <section id="features" className="py-5">
      <Container>
        <h2 className="text-center mb-4">Why Choose FI Money?</h2>
        <Row>
          {features.map((feature, index) => (
            <Col md={4} key={index}>
              <Card className="text-center">
                <Card.Body>
                  <Card.Title>{feature.title}</Card.Title>
                  <Card.Text>{feature.text}</Card.Text>
                </Card.Body>
              </Card>
            </Col>
          ))}
        </Row>
      </Container>
    </section>
  );
};

export default Features;
